.test-table {
    // position: relative;
    // top: 35rem;
    // left: 50%;
    // transform: translate(-50%, -50%);
    &:last-child {
        padding-bottom: 10rem;
    }

    &--p1db {
        width: 130%;
    }

    &__p {
        font-size: 0.7rem ;
    }
}


.results-table {
    margin-top: 5rem;
    position: relative;
    right: -60%;
    transform: translate(-100%);
    border: 1px solid black;

    &--p1db {
        font-size: 1rem;
    }

    &--inside {
        position: relative;
        box-sizing: border-box;
        width: 100%;
        border-collapse: collapse;
    }

    &__fulllinkdl {
        right: -50% ;
        transform: translate(-50%);
        font-size: 1rem;
        margin-bottom: 10rem;
    }
}

